<template>
     <div id="site">
          <div id="login_container">
               <router-link id="home" to="/">
                    <img src="../assets/logo.svg" id="logo" />
               </router-link>
               <div id="login_form">
                    <div id="login_fields">
                         <label for="login_email">Email:</label>
                         <div class="formfield">
                              <img id="login_email_icon" src="../assets/mail.svg" />
                              <input
                                   v-model="username"
                                   id="login_email"
                                   type="email"
                                   required
                                   v-on:keyup.enter="loginRequest"
                              />
                         </div>
                         <label for="login_password">Password:</label>
                         <div class="formfield">
                              <img id="login_password_icon" src="../assets/lock.svg" />
                              <img
                                   id="login_eye_icon"
                                   src="../assets/password_open.svg"
                                   v-model="passwordShow"
                                   v-on:click="togglePassword"
                              />
                              <input
                                   v-model="password"
                                   id="login_password"
                                   type="password"
                                   required
                                   :keyup.enter="loginRequest"
                              />
                         </div>
                    </div>
                    <span id="login_error"></span>
                    <div id="login_extras">
                         <div id="rememberme">
                              <input v-model="rememberMe" id="check" type="checkbox" />
                              <label v-on:click="toggleRemember">Remember Me</label>
                         </div>

                         <label id="login_forgot">I forgot my password!</label>
                    </div>
                    <input
                         id="login_submit"
                         type="submit"
                         value="Log in"
                         v-on:click.prevent="loginRequest"
                    />
               </div>
          </div>
     </div>
</template>

<script>
import axios from "axios";
import jwt_decode from "jwt-decode";
export default {
     name: "Login",
     data() {
          return {
               passwordShow: false,
               rememberMe: false,
               username: "",
               password: ""
          };
     },
     methods: {
          toggleRemember() {
               if (this.rememberMe) {
                    this.rememberMe = false;
               } else {
                    this.rememberMe = true;
               }
          },
          togglePassword() {
               if (this.passwordShow) {
                    $("#login_password").attr("type", "password");
                    this.passwordShow = false;
               } else {
                    $("#login_password").attr("type", "text");
                    this.passwordShow = true;
               }
          },
          valid() {
               //empty
               if (this.username == "") {
                    $("#login_error").text("Error: please enter your email.");
                    return false;
               //valid
               } else if (!$("#login_email").prop('validity').valid) {
                    $("#login_error").text("Error: please enter a valid email.");
               //valid
               } else if (this.password.length < 10) {
                    $("#login_error").text("Error: passwords must be at least 10 characters long.");
               } else {
                    return true;
               }
          },
          loginRequest() {
               if (this.valid()) {
                    this.$store.set("staySignedIn", this.rememberMe);
                    var formData =
                         '{"username":"' +
                         this.username +
                         '","password":"' +
                         this.password +
                         '"}';
                    axios({
                         method: "post",
                         url: this.$store.get("apiUrl") + "users/login",
                         data: formData,
                         headers: {}
                    })
                         .then(response => {
                              let token = response.data.encoded;
                              this.$store.set("token", token );
                              this.$store.set(
                                   "username",
                                   response.data.username
                              );
                              let decoded = jwt_decode(token);
                              console.log(decoded)
                              this.$store.set("email", decoded.data);
                              this.$router.push("home");
                         })
                         .catch(function(error) {
                              $("#login_submit").removeAttr("disabled");
                              console.log(error)
                              $("#login_error").text(
                                   "Error: " + error.response.data.detail
                              );
                         });
               } else {
               }
          }
     },
     components: {},
     mounted: function() {
          this.show = false;
     }
};
</script>

